.logo{
  display: block;
  width: 7.75em;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
}

.header{
  padding: 1.25em 0;
  &__inner{
    justify-content: space-between;
  }
  .logo{
    margin-right: 10em;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.menu{
  flex-grow: 1;
  justify-content: space-between;
  &__btn{
    margin-left: 2em;
  }
}

.nav{
  &__link{
    @include lastM(0 1.875em 0 0);
    color: rgba($black,.4);
    &._active,
    &:hover{
      color: rgba($black,.7);
    }
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      justify-content: space-between;
    }
    .logo{
      margin: 0;
      position: relative;
      z-index: 21;
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
      .logo{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: linear-gradient(135deg, #261d2f 0%, #211d2f 100%);
    overflow-y: auto;
    flex-direction: column;
    align-items: flex-start;
    transform: translateX(-120%);
    z-index: 20;
    padding: 5em .9375em 2em;
    transition: all .3s;

    &__btn{
      margin: 3em 0 0;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      @include lastM(0 0 1.25em);
      color: rgba(#fff,.4);
      &:hover,
      &._active{
        color: rgba(#fff,.7);
      }
    }
  }

  .burger{
    @include center;
    width: 2.625em;
    height: 2.41875em;
    border-radius: 0.3125em;
    position: relative;
    z-index: 21;
    img{
      @include centerAbsolute;
      width: 1.375em;
    }
    ._view2{
      width: 1.75em;
      opacity: 0;
    }
  }
}
