.about-rating{
  padding: 3.125em 0;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.625em * 3 / 4));
    gap: 2.6125em;
    margin-top: 1.875em;
  }
}

.rating-advantage{
  padding: 2.125em 1em 2.68em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1.25em;
  background: $primary;
  color: #fff;
  &__icon{
    height: 2.5em;
    img{
      display: block;
      max-width: 2.5em;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: 1.5em;
    @include grid100gap(.31em);
  }
  &__title{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
  }
  &__desc{
    font-size: .9375em;
    line-height: 150%;
  }
}

.info{
  padding: 3.125em 0;
  .text{
    margin-top: 1.25em;
  }
}

.text{
  ul,ol,
  p{
    @include lastM(0 0 1em);
  }
  li,
  p{
    font-size: 1.25em;
    line-height: 150%;
  }
  ul,ol{
    padding-left: .5em;
  }
  li{
    padding-left: 1em;
    position: relative;
    &:before{
      content: '•';
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  ol{
    counter-reset: section;
    li{
      counter-increment: section;
      &:before {
        content: counter(section) '. ';
        position: absolute;
        width: auto;
        height: auto;
        background-color: transparent;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
}

.main{
  padding: 1.5em 0 3.125em;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 25em) 23.75em;
    gap: 1.25em;
  }
  &__nav{
    margin-top: 2.5em;
    padding-top: 2.5em;
    display: flex;
    justify-content: center;
    border-top: 1px solid rgba($black,.3);
  }
  &__btn{
    width: 22.5em;
    max-width: 100%;
    height: 3.75em;
  }
}

.main-banner{
  grid-column: 1/2;
  grid-row: 1/3;
  background: linear-gradient(90.00deg, rgba(0, 174, 236, 0.77),rgba(253, 242, 0, 0.77) 154.233%);
  border-radius: .625em;
  padding: 5em 1.25em;
  position: relative;
  color: #fff;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  .h1{
    max-width: 11em;
  }
  &__desc{
    margin-top: 1.125em;
    max-width: 40.625em;
  }
  &__nav{
    display: flex;
    margin-top: 1.125em;
  }
  &__link{
    padding: 0 1.5625em;
    font-weight: 400;
  }
}

.banner{
  @include imgCover(55.2%);
  border-radius: .625em;
  min-height: 100%;
}


@media screen and (max-width: 992px){
  .about-rating{
    padding: 2.5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.75em;
    }
  }

  .info{
    padding: 2.5em 0;
  }

  .text{
    li,
    p{
      font-size: .9375em;
    }
  }

  .main{
    padding: .3125em 0 2.5em;
    &__inner{
      grid-template-columns: 100%;
    }
    .banner{
      display: none;
    }
  }

  .main-banner{
    padding: 1.5em 1.25em;
  }
}


@media screen and (max-width: 576px){
  .about-rating{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .rating-advantage{
    border-radius: .625em;
  }
}
