.catalog{
  margin-top: 2.5em;
}

.catalog-card{
  padding-bottom: 2.5em;
  margin-bottom: 2.5em;
  border-bottom: 1px solid rgba($black,.3);
  display: grid;
  grid-template-columns: 16.375em calc(100% - 21.375em - 11.25em) 11.25em;
  gap: 1.25em 2.5em;
  &:last-child{
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
  }
  &__col1{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__img{
    border: 1px solid #d0d0d0;
    width: 10em;
    height: 10em;
    border-radius: .3125em;
    @include center;
    padding: 1em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__place{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: $primary;
    font-weight: 700;
    &-num{
      font-size: 5em;
    }
  }
  @media screen and (min-width: 577px){
    &__col2{
      grid-row: 1/3;
      grid-column: 2/3;
    }
    &__col3{
      grid-row: 1/3;
      grid-column: 3/4;
      display: flex;
      flex-direction: column;
    }
    &__col4{
      grid-column: 1/2;
      grid-row: 2/3;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 120%;
    text-transform: uppercase;
    font-weight: 900;
  }
  &__info{
    margin-top: .625em;
  }
  &__reviews{
    &-count{
      margin-left: 1.25em;
      color: rgba($primary,.8);
    }
  }
  .status-item{
    margin-left: 1.25em;
  }
  &__text{
    margin-top: .625em;
    p{
      font-size: .875em;
      @include lastM(0 0 .7em);
    }
  }
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    @include grid100gap(.25em);
    max-width: 9.6875em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
    @include grid100gap(.4375em);
  }
  &__btn{
    height: 2.375em;
    span{
      font-size: .8125em;
    }
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.625em;
  }
  &__tag{
    padding: 0 .875em;
    background: #f0f0f0;
    height: 1.8125em;
    border-radius: .25em;
    margin: .625em .625em 0 0;
    font-weight: 700;
    color: #788;
    @include center;
    span{
      font-size: .8125em;
    }
  }
  &__adv{
    height: 100%;
    width: 3.75em;
    padding: 1.25em 0;
    background-color: $primary;
    color: #fff;
    border-radius: .3125em;
    font-weight: 700;
    @include center;
    span{
      writing-mode: tb-rl;
      font-size: 1.25em;
      transform: rotate(-180deg);
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.rating{
  &__stars{
    margin: 0 .3125em;
    flex-shrink: 0;
    width: 6.25em;
  }
}

.status-item{
  display: flex;
  align-items: center;
  color: rgba($black,.5);
  img{
    width: 1.25em;
    margin-right: .3125em;
  }
}

.main-slider{
  margin-top: 2.5em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
        gap: 1.25em;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
  &__img{
    @include imgCover(100%);
    border-radius: .3125em;
  }
}


@media screen and (max-width: 992px){
  .catalog-card{
    font-size: .75em;
    grid-template-columns: 15em calc(100% - 28.5em) 11em;
    gap: 1.25em;
    &__info{
      display: block;
    }
    &__status{
      margin-top: 1.25em;
    }
    .status-item{
      &:first-child{
        margin-left: 0;
      }
    }
  }

  .main-slider{
    .swiper{
      &-slide{
        width: 11.25em;
        padding-right: 1.25em;
      }
    }
    &__img{
      @include imgCover(100%);
      border-radius: .3125em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-card{
    grid-template-columns: 100%;
    font-size: 1em;
    gap: 2.5em;
    &__col3{
      order: 10;
    }
    &__col4{
      margin-top: -1.875em;
    }
    &__place{
      &-num{
        font-size: 3.75em;
      }
    }
    &__img{
      width: 7.375em;
      height: 4.875em;
      padding: .5em;
    }
    &__item{
      max-width: 100%;
    }
    &__nav{
      grid-template-columns: repeat(2,calc(50% - .4375em / 2));
    }
    &__tags{
      width: calc(100% + .5em);
    }
    &__tag{
      margin-right: .3125em;
      height: 1.6875em;
      span{
        font-size: .75em;
      }
    }
    &__adv{
      width: 7.5em;
      span{
        writing-mode: unset;
        font-size: .875em;
        transform: unset;
      }
    }
  }
}
