.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border-radius: 0.3125em;
    border: none;
    padding: 0 1.25em 0 2.5em;
    height: 3.5625em;
    width: 100%;
    font-size: 1em;
    background-color: #efefef;
    color: $black;
    &::placeholder {
      color: rgba($black,.5);
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: none;
    &~.fg__icon{
      top: 1.25em;
      transform: unset;
    }
  }
  &__icon{
    position: absolute;
    left: .625em;
    top: 50%;
    transform: translateY(-50%);
    max-width: 1.25em;
    max-height: 1.375em;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.filter-radio,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    padding-left: 1.625em;
    min-height: 1em;
    font-weight: 700;
    color: rgba($black,.5);
    display: flex;
    align-items: center;
    span{
      font-size: .875em;
      line-height: 130%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background: #d9d9d9;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      background-image: url(../img/check2.svg);
    }
  }
}


