@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.875em;
    border-radius: .3125em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .8125em;
    }
}

.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        border-color: darken($primary,10%);
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: #333;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}
