.filter{
  margin-top: 1.25em;
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 25em) 23.75em;
    gap: 1.25em;
  }
  &__search{
    input{
      height: 3.125em;
    }
  }
  &__bottom{
    margin-top: .75em;
    display: flex;
    align-items: center;
    width: calc(100% - 25em);
  }
  &__title{
    display: flex;
    align-items: center;
    font-weight: 700;
    color: #333;
    margin-right: 1em;
    img{
      width: 1.125em;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
  &__items{
    display: flex;
    flex-grow: 1;
    align-items: center;
  }
}

.filter-group{
  display: flex;
  align-items: center;
  margin-right: .625em;
  flex-grow: 1;
  &:last-child{
    margin-right: 0;
  }
  &__title{
    font-size: .75em;
    line-height: 140%;
  }
}

.filter-select{
  flex-grow: 1;
  margin-left: .3125em;
  width: 15em;
  .selectize-input{
    padding: .4375em .625em;
    border: none;
    background-color: #f0f0f0 !important;
    background-image: url(../img/arrow.svg) !important;
    background-size: .75em auto !important;
    background-position: right .625em center !important;
    background-repeat: no-repeat !important;
    border-radius: .3125em;
    height: 1.875em;
    cursor: pointer;
    font-size: 1em !important;
    &:after{
      display: none !important;
    }
    &:not(.has-items){
      .item{
        color: rgba(#888,.7);
      }
    }
    &>input{
      &::placeholder{
        color: rgba(#888,.7) !important;
      }
    }
  }
  .item{
    font-size: .75em;
    color: rgba($black,.7);
  }
  .selectize-dropdown{
    font-size: 1em;
    border: none;
    padding: .625em;
    box-shadow: 0 .625em 2.5em 0 rgba(0, 0, 0, 0.1);
    background: #fff;
    &-content{
      @include grid100gap(.625em);
      padding: .5em;
      @include scroll;
    }
  }
  .option{
    padding: 0;
    font-size: .75em;
    line-height: 140%;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: 1.17em;
      height: .83em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      opacity: 0;
      flex-shrink: 0;
      margin-left: auto;
    }
    &.active{
      background-color: transparent;
    }
    &.selected{
      color: $black;
      background-color: transparent;
      &:after{
        opacity: 1;
      }
    }
  }
}

.filter-item{
  flex-grow: 1;
  min-width: 10.25em;
  margin-left: .3125em;
  position: relative;
  &._open{
    .filter-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    padding: 0 .625em;
    background: #f0f0f0;
    border-radius: .3125em;
    height: 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    &:after{
      content: '';
      width: .75em;
      height: .5em;
      background-image: url(../img/arrow.svg);
      @include bgImgSize(100%);
      margin-left: .5em;
    }
  }
  &__title{
    font-size: .75em;
    color: #888;
    opacity: .7;
  }
  &__hidden{
    z-index: 10;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    min-width: 13.5em;
    padding: 1.25em;
    box-shadow: 0 .625em 2.5em 0 rgba(0, 0, 0, 0.1);
    background: #fff;
    @include grid100gap(.625em);
  }
}

.filter-radio{
  label{
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:after{
      content: '';
      width: .875em;
      height: .625em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      opacity: 0;
      flex-shrink: 0;
      margin-left: .5em;
    }
  }
  input:checked~label:after{
    opacity: 1;
  }
  span{
    font-size: .75em;
    line-height: 140%;
  }
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
}

.selected-filter{
  margin: .75em .75em 0 0;
  height: 2.0625em;
  padding: 0 .625em;
  background-color: $primary;
  border-radius: .3125em;
  display: flex;
  align-items: center;
  color: #fff;
  &__remove{
    width: .625em;
    @include center;
    margin-left: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.sort{
  display: flex;
  .filter-item{
    flex-grow: 0;
    margin: 0;
    &__show{
      background-color: $primary;
      padding: 0 2em;
      height: 3.125em;
      &:after{
        display: none;
      }
      img{
        margin-right: .625em;
        width: 1.0625em;
      }
    }
    &__title{
      color: #fff;
      font-size: .875em;
      font-weight: 700;
      opacity: 1;
    }
    &__hidden{
      width: auto;
      min-width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .filter{
    margin-top: 2.5em;
    position: relative;
    &__top{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__search{
      grid-column: 1/-1;
    }
    &__title{
      position: absolute;
      right: 0;
      top: 4.375em;
      width: calc(50% - .625em);
      height: 3.125em;
      justify-content: center;
      background: #f0f0f0;
      border-radius: .3125em;
      margin: 0;
    }
    &__bottom{
      width: 100%;
      margin-top: 1.5em;
    }
    &__items{
      padding: .625em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .sort{
    display: block;
    .filter-item{
      width: 100%;
      &__show{
        width: 100%;
        justify-content: center;
      }
    }
  }

  .filter-group{
    display: block;
    margin: 0;
    &__hidden{
      width: 100%;
      min-width: auto;
    }
  }

  .filter-select,
  .filter-item{
    margin: .3125em 0 0;
    width: 100%;
  }
}


@media screen and (max-width: 576px){
  .filter{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
