.footer{
  background: #111;
  padding: 3.75em 0;
  color: #fff;
  margin-top: 5em;
  &__top{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 2.5em 1.25em;
  }
  &__c{
    margin-top: 1.25em;
    opacity: .46;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1em;
  }
  &__link{
    font-size: .9375em;
    line-height: 120%;
    color: rgba(#fff,.7);
    @include lastM(0 0 .5em);
  }
  &__bottom{
    margin-top: 1.25em;
    padding-top: 1.25em;
    border-top: 1px solid rgba(#fff,.1);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    &-text{
      margin-top: 1em;
      font-size: .875em;
      line-height: 140%;
      color: rgba(#fff,.7);
    }
  }
  &__contact{
    display: flex;
    align-items: center;
    font-weight: 900;
    &-link{
      background: #3560cf;
      padding: 0 .3125em;
      display: flex;
      align-items: center;
      border-radius: 18.75em;
      height: 1.5em;
      margin-left: .3125em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.1875em 0;
    &__top{
      grid-template-columns: 100%;
    }
    &__bottom{
      display: block;
      margin-top: 1em;
      border: none;
    }
    &__contact{
      margin-top: 1em;
    }
  }
}


