.catalog-item{
  padding: 1.25em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 25em) 22.5em;
    gap: 2.5em;
  }
  .main-banner{
    min-height: 27.5em;
    display: flex;
    align-items: flex-end;
    padding: 2.5em 1.25em;
    grid-row: unset;
    &__desc{
      margin-top: .625em;
    }
  }
}

.catalog-slider{
  height: 100%;
  position: relative;
  .swiper{
    &-container{
      height: 100%;
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-button{
      width: 2.5em;
      height: 2.5em;
      @include center;
      border-radius: 50%;
      background-color: #fff;
      box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.15);
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      &-prev{
        left: -1.25em;
      }
      &-next{
        right: -1.25em;
      }
      svg{
        width: .5em;
      }
    }
  }
}

.catalog-slide{
  background: linear-gradient(90.00deg, rgba(0, 174, 236, 0.77),rgba(253, 242, 0, 0.77) 154.233%);
  border-radius: .625em;
  padding: 2.5em 3.75em;
  position: relative;
  color: #fff;
  height: auto;
  display: flex;
  align-items: flex-end;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__logo{
    border: 1px solid #d0d0d0;
    width: 10em;
    height: 10em;
    background-color: #fff;
    border-radius: .3125em;
    padding: 1em;
    @include center;
    margin-bottom: .625em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__place{
    position: absolute;
    z-index: 3;
    top: 2.5em;
    right: 3.75em;
    background-color: $primary;
    padding: 0 1.25em;
    height: 2.875em;
    border-radius: .3125em;
    font-weight: 700;
    color: #fff;
    display: flex;
    align-items: center;
  }
  .catalog-card__reviews-count{
    color: #fff;
  }
  .status-item{
    color: #fff;
    font-weight: 700;
    margin-left: 1.25em;
  }
}

.contacts{
  padding: 1.25em;
  background-color: $primary;
  height: 100%;
  color: #fff;
  border-radius: .625em;
  &__items{
    @include grid100gap(1.5em);
  }
  &__item{
    @include grid100gap(.125em);
  }
  a{
    &:hover{
      color: rgba(#fff,.7);
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: .3125em;
    &-item{
      @include center;
      margin-right: .5625em;
      img{
        max-width: 1.5em;
        max-height: 1.25em;
        display: block;
      }
    }
  }
}

.info{
  padding: 0;
  margin-top: 1.875em;
}

.gallery{
  margin-top: 5em;
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
  &__item{
    @include imgCover(100%);
    border-radius: .3125em;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    width: 22.5em;
    max-width: 100%;
    height: 3.75em;
  }
}

.page-nav{
  margin-top: 1.25em;
  &__links{
    display: flex;
  }
  &__link{
    flex-shrink: 0;
    font-weight: 700;
    font-size: .9375em;
    line-height: 150%;
    padding-bottom: .67em;
    color: rgba($black,.5);
    margin-right: 2.66em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: .2em;
      width: 50%;
      background-color: $primary;
      opacity: 0;
      transition: inherit;
    }
    &._active{
      color: rgba($black,.7);
      &:before{
        opacity: 1;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .catalog-slider{
    .swiper{
      &-button{
        display: none;
      }
    }
  }

  .catalog-slide{
    padding: 5.625em 1.25em 1.25em;
    .h1{
      font-size: 2.625em;
    }
    .catalog-card__status{
      margin-top: .625em;
    }
    .status-item{
      &:first-child{
        margin-left: 0;
      }
    }
    &__place{
      top: 1.25em;
      right: 1.25em;
      height: 1.875em;
    }
  }

  .info{
    margin-top: 2.5em;
  }

  .gallery{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    }
  }

  .page-nav{
    .container{
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .gallery{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }
}
