.services{
  margin-top: 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2.5em 2em;
    margin-top: 1.25em;
  }
  &__subtitle{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
    color: $primary;
    opacity: .7;
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
}

.service-item{
  display: flex;
  align-items: center;
  min-height: 1.4375em;
  &:after{
    content: '';
    flex-grow: 1;
    min-width: 1em;
    height: 1px;
    border-top: 1px dashed #000;
    opacity: .3;
    margin: 0 .3125em;
  }
  &__desc{
    order: 2;
  }
}

.results{
  margin-top: 5em;
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 2.5em 1.25em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__btn{
    padding: 0 2.375em;
    height: 3.75em;
  }
}

.result-card{
  &__img{
    @include imgCover(72%);
    border-radius: .3125em;
  }
  &__info{
    margin-top: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
  }
  &__date{
    white-space: nowrap;
    margin-left: 1em;
    color: rgba($black,.4);
  }
  &__desc{
    font-size: .9375em;
    line-height: 150%;
    color: rgba($black,.6);
    margin-top: .67em;
  }
}

.feedback{
  margin-top: 5em;
  &__desc{
    margin-top: 1.25em;
    max-width: 48em;
  }
}

.feedback-form{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 1.25em 2.5em;
  &__text{
    @media screen and (min-width: 993px){
      grid-column: 2/3;
      grid-row: 1/4;
      textarea{
        height: 100%;
      }
    }
  }
  &__wrap{
    display: flex;
    align-items: center;
    grid-column: 1/-1;
  }
  &__btn{
    padding: 0 3.75em;
    height: 3.375em;
    margin-right: 2.5em;
  }
}

.reviews{
  margin-top: 3.75em;
  &__items{
    margin-top: 3.75em;
  }
  .results__nav{
    margin-top: 2.5em;
  }
}

.review-card{
  max-width: 47.5em;
  padding-bottom: 2.5em;
  margin-bottom: 2.5em;
  border-bottom: 1px solid rgba($black,.3);
  &:last-child{
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 3.75em;
    height: 3.75em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 50%;
    }
  }
  &__date{
    margin-top: .125em;
    color: rgba($black,.5);
  }
  &__text{
    margin-top: 1.33em;
  }
}

.place{
  margin-top: 5em;
}

.map{
  height: 21.875em;
  margin-top: 1.25em;
}


@media screen and (max-width: 992px){
  .services {
    &__grid {
      grid-template-columns: 100%;
    }
  }

  .results{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .feedback-form{
    grid-template-columns: 100%;
    &__wrap{
      display: block;
    }
    &__btn{
      margin: 0 0 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .results{
    &__grid {
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
